:root {
  --pf-v5-global--FontSize--md: 14px;
  --pf-global--FontSize--md: 14px;
  --pf-v5-global--FontSize--sm: 13px;
  --pf-global--FontSize--sm: 13px;
}

.pipeline-metrics-dashboard__body {
  background-color: var(--pf-v5-global--BackgroundColor--200);
  margin-bottom: 0 !important;
  margin-top: 0;
}

.pipeline-metrics-dashboard__body-content {
  margin-top: 1.5rem;
}

.pipeline-metrics-dashboard__toolbar {
  margin-bottom: 0 !important;
  padding: var(--pf-v5-global--spacer--md) 1.5rem 0;
}

.pipeline-metrics-empty-state {
  background-color: var(--pf-v5-global--BackgroundColor--200);
}

.pipeline-metrics__pipelinerun-taskrun-card-body {
  padding: 10px 15px 10px 10px;
}

