.odc-task-list-node {
  border: 1px solid var(--pf-v5-global--BorderColor--light-100);
}

.odc-task-list-node__container {
  overflow: hidden;
  box-shadow: var(--pf-v5-c-dropdown__menu--BoxShadow);
}

.odc-task-list-node__trigger-background {
  stroke: var(--pf-v5-global--BorderColor--100);
  fill: var(--pf-v5-global--BackgroundColor--light-100);
}

.odc-task-list-node__trigger-background.is-disabled {
  fill: var(--pf-v5-global--disabled-color--300);
}

.odc-task-list-node__trigger-underline {
  fill: var(--pf-v5-global--Color--100);
}

.odc-task-list-node__trigger-underline--hover {
  fill: var(--pf-v5-global--link--Color);
}

.odc-task-list-node__trigger {
  font-size: var(--pf-v5-global--FontSize--md);
  text-anchor: middle;
  dominant-baseline: middle;
  cursor: pointer;
}

.odc-task-list-node__trigger-disabled {
  fill: var(--pf-v5-global--disabled-color--100);
}

.odc-task-list-node__list-items {
  max-height: 350px;
  overflow: auto;
}

.odc-task-list-node__divider {
  border-color: var(--pf-v5-global--BorderColor--100);
  margin: 0;
}

.odc-task-list-node__render-text {
  fill: var(--pf-v5-global--Color--100);
}

