.odc-pipeline-secret-section {
  margin-top: var(--pf-v5-global--spacer--sm);
  padding-left: var(--pf-v5-global--spacer--lg);
}

.odc-pipeline-secret-section__secrets {
  padding-left: var(--pf-v5-global--spacer--md);
}

.odc-pipeline-secret-section__secret-form {
  border: 1px dashed var(--pf-v5-global--BorderColor--100);
  padding: var(--pf-v5-global--spacer--md);
}

.odc-pipeline-secret-section__secret-action {
  padding-left: 0;
}

.odc-pipeline-expandable-content {
  margin-top: var(--pf-v5-global--spacer--sm);
}

