.odc-pipeline-run-logs {
  display: flex;
  flex: 1;
  width: 100%;
  padding: var(--pf-v5-global--spacer--xl) 0;
}

.odc-pipeline-run-logs__nav {
  padding-top: 0;
  list-style-type: none;
}

.odc-pipeline-run-logs__nav .co-icon-and-text {
  position: relative;
  top: .35em;
}

.odc-pipeline-run-logs__namespan {
  margin-left: var(--pf-v5-global--spacer--sm);
  white-space: nowrap;
  overflow: hidden;
  text-overflow: ellipsis;
}

.odc-pipeline-run-logs__log {
  height: 100%;
  padding-top: var(--pf-v5-global--spacer--md);
}

.odc-pipeline-run-logs__logtext {
  position: relative;
  background: var(--pf-v5-global--palette--black-1000);
  color: var(--pf-v5-global--palette--black-100);
  height: 100%;
  padding: var(--pf-v5-global--spacer--sm) var(--pf-v5-global--spacer--md);
}

.odc-pipeline-run-logs__logsnippet {
  margin-top: var(--pf-v5-global--spacer--sm);
}

.odc-pipeline-run-logs__tasklist {
  max-width: 30%;
  min-width: 208px;
  margin-top: var(--pf-v5-global--spacer--lg);
}

.odc-pipeline-run-logs__container {
  flex: 1;
  padding-right: var(--pf-v5-global--spacer--xl);
}

.odc-pipeline-run-logs__container .co-m-pane__body {
  padding: 0px;
}

