.opp-vulnerabilities {
  display: flex;
  flex-wrap: wrap;
  gap: var(--pf-global--spacer--sm);
}

.opp-vulnerabilities__severity {
  align-items: center;
  display: flex;
  flex-wrap: nowrap;
  gap: var(--pf-global--spacer--xs);
}

.opp-vulnerabilities__severity-status {
  align-items: center;
  display: flex;
  flex-wrap: nowrap;
  gap: var(--pf-global--spacer--xs);
}

.opp-vulnerabilities__severity-count {
  font-weight: var(--pf-global--FontWeight--bold);
}

