.opp-quick-search-details {
  padding-top: var(--pf-v5-global--spacer--md);
}

.opp-quick-search-details__form-button,
.opp-quick-search-details__version-dropdown {
  margin: var(--pf-v5-global--spacer--md) 0px !important;
  width: max-content;
}

.opp-quick-search-details__version-dropdown-item {
  min-width: 7em;
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.opp-quick-search-details__provider {
  color: var(--pf-v5-global--Color--200);
}

.opp-quick-search-details__description {
  font-size: 15px !important;
}

.opp-quick-search-details__hublink {
  margin-left: var(--pf-v5-global--spacer--sm);
}

.opp-quick-search-details__badges-section {
  margin: var(--pf-v5-global--spacer--md) 0;
  --pf-v5-l-stack--m-gutter--MarginBottom: var(--pf-v5-global--spacer--sm) !important;
}

