.odc-trigger-binding-section__variable-container {
  border: 1px solid var(--pf-v5-global--BorderColor--100);
  margin-left: var(--pf-v5-global--spacer--lg);
  padding: var(--pf-v5-global--spacer--sm);
}

.odc-trigger-binding-section__variable-help-text {
  margin-left: var(--pf-v5-global--spacer--lg);
  margin-top: var(--pf-v5-global--spacer--xs);
}

.odc-trigger-binding-section__variable-descriptor {
  margin-bottom: var(--pf-v5-global--spacer--sm);
}

.odc-trigger-binding-section__variable-badge {
  margin-bottom: var(--pf-v5-global--spacer--xs);
  margin-right: var(--pf-v5-global--spacer--xs);
}

