.odc-pipeline-vis-task {
  fill: var(--pf-v5-global--BackgroundColor--light-100);
  stroke: var(--pf-v5-global--BorderColor--light-100);
  stroke-width: 1;
  cursor: default;
}

.odc-pipeline-vis-task.is-selected {
  stroke: #73bcf7;
  stroke-width: 2;
}

.odc-pipeline-vis-task.is-linked {
  cursor: pointer;
}

.odc-pipeline-vis-task--icon-stop {
  transform: rotate(0);
}

.odc-pipeline-vis-task--icon-spin {
  filter: blur(0);
  -webkit-filter: blur(0);
  transform-box: fill-box;
  transform-origin: center;
}

.odc-pipeline-vis-task-text {
  cursor: default;
  dominant-baseline: middle;
  fill: var(--pf-v5-global--Color--100);
}

.odc-pipeline-vis-task-text.is-text-center {
  text-anchor: middle;
}

.odc-pipeline-vis-task-text.is-linked {
  cursor: pointer;
}

