.odc-multi-stream-logs {
  display: flex;
  flex: 1;
  flex-direction: column;
  height: 100%;
}

.odc-multi-stream-logs--fullscreen {
  background-color: var(--pf-v5-global--BackgroundColor--100);
  padding: var(--pf-v5-global--spacer--sm);
}

.odc-multi-stream-logs__icon {
  margin-right: var(--pf-v5-global--spacer--xs);
}

.odc-multi-stream-logs__divider {
  color: var(--pf-v5-global--palette--black-600);
  margin: 0 var(--pf-v5-global--spacer--sm);
}

.odc-multi-stream-logs__button {
  margin-right: 0;
}

.odc-multi-stream-logs__container {
  display: flex;
  flex: 1;
  flex-direction: column;
  background-color: var(--pf-v5-global--palette--black-1000);
  color: var(--pf-v5-global--Color--light-100);
  font-family: Menlo,Monaco,"Courier New",monospace;
  overflow-y: scroll;
  position: relative;
}

.odc-multi-stream-logs__container__logs {
  position: absolute;
  width: 100%;
}

.odc-multi-stream-logs__taskName {
  background-color: var(--pf-v5-global--BackgroundColor--dark-300);
  padding: var(--pf-v5-global--spacer--sm) var(--pf-v5-global--spacer--md);
  color: var(--pf-v5-global--Color--light-100);
}

.odc-multi-stream-logs__taskName__loading-indicator {
  margin-left: var(--pf-v5-global--spacer--sm);
}

