.opp-pipeline-run-list__signed-indicator {
  display: inline-block;
  --pf-c-table--cell--Color: var(--pf-global--BackgroundColor--dark-transparent-100);
  margin-left: var(--pf-global--spacer--sm);
}

.opp-pipeline-run-list__signed-indicator>img {
  height: var(--pf-global--FontSize--lg);
  position: relative;
  top: 4px;
}

.opp-pipeline-run-list__results-indicator {
  display: inline-block;
  margin-left: var(--pf-global--spacer--xs);
}

.opp-pipeline-run-list__results-indicator>svg {
  color: var(--pf-global--Color--400);
}

