.opp-task-sidebar-when-expression {
  margin-bottom: var(--pf-v5-global--spacer--lg);
}

.opp-task-sidebar-when-expression__section {
  margin-bottom: var(--pf-v5-global--spacer--md);
  border: 1px dashed var(--pf-v5-global--BorderColor--100);
  padding: var(--pf-v5-global--spacer--md);
}

.opp-task-sidebar-when-expression__control-button-wrapper {
  display: flex;
  justify-content: flex-end;
}

.opp-task-sidebar-when-expression__control-button {
  padding: 0;
  color: var(--pf-v5-c-button--m-link--Color);
}

.opp-task-sidebar-when-expression__control-label {
  margin-left: var(--pf-v5-global--spacer--xs);
}

.opp-task-sidebar-when-expression__field {
  margin-right: var(--pf-v5-global--spacer--md);
}

