.opp-task-sidebar {
  width: 550px;
  max-width: 100%;
  position: absolute;
  background: var(--pf-v5-global--BackgroundColor--100);
  right: 0;
  height: 100%;
  box-shadow: var(--pf-v5-global--BoxShadow--md);
}

.opp-task-sidebar__header {
  background: var(--pf-v5-global--BackgroundColor--100);
  z-index: 10000;
}

.opp-task-sidebar__content {
  padding: var(--pf-v5-global--spacer--lg);
  overflow-y: auto;
}

.opp-task-sidebar__param,
.opp-task-sidebar__resource,
.opp-task-sidebar__workspace,
.opp-task-sidebar__when-expressions {
  margin-bottom: var(--pf-v5-global--spacer--md);
}

.opp-task-sidebar__paragraph {
  margin-bottom: var(--pf-v5-c-content--MarginBottom);
}

.opp-task-sidebar h2 {
  margin: 0 0 var(--pf-v5-global--spacer--sm) 0;
}

